import {IsDistributor,ApplyDistributorlist} from 'api/user'
export default{
	data(){
		return{
			baseUrl:this.$store.state.SRC+"/config/app",
			data:{},
			state:{},
			load:false
		}
		
	},
	onPullDownRefresh() {
		this.initData()	
	},
	methods:{
		open(url){
			uni.navigateTo({
				url:url
			})
		},
		initData(){
			ApplyDistributorlist().then(res=>{
				if(res.result){
					this.data = res
					this.load = true
				}
			})
		}
	},
	onShow() {
		this.$store.ready(state=>{
			this.state = state
			this.initData()
		})	
	}
}
